import React from 'react';
import { useOnboardingScreen } from '@popup/shared/hooks/useOnboardingScreen';
import { useChangeDevice } from '@popup/project/youboost-neo/hooks/useChangeDevice';
import { useNavigateToPremiumInfo } from '@popup/project/youboost-neo/hooks/useNavigateToPremiumInfo';
import { useNavigateToTariffs } from '@popup/project/youboost-neo/hooks/useNavigateToTariffs';
import { useNavigateToUnavailableTrial } from '@popup/project/youboost-neo/hooks/useNavigateToUnavailableTrial';
import { useNavigateToGiftAdvertising } from '@popup/project/youboost-neo/hooks/useNavigateToGiftAdvertising';
import { usePopupReady } from '@popup/project/youboost-neo/hooks/usePopupRedy';
import { OnboardingStep } from '@popup/project/youboost-neo/components/Onboarding/ui/constants';
import { Onboarding } from '@popup/project/youboost-neo/components/Onboarding';

interface Props {
  children: React.ReactNode;
}

export const MainLayout: React.FC<Props> = ({ children }) => {
  const { onboardingStatus, isLoading: isOnboardingStatusLoading } =
    useOnboardingScreen();

  useChangeDevice();
  useNavigateToPremiumInfo();
  useNavigateToTariffs();
  useNavigateToUnavailableTrial();
  useNavigateToGiftAdvertising();
  usePopupReady();

  if (isOnboardingStatusLoading) {
    return null;
  }

  if (onboardingStatus !== OnboardingStep.Done) {
    return <Onboarding onboardingStatus={onboardingStatus} />;
  }

  return <>{children}</>;
};
